<template>
  <div class="pageContent">
    <!-- 数据列表 - 筛选-->
    <el-card class="operate-container mt10" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="入账时间：">
            <el-date-picker v-model="searchList.time"
              type="daterange"
              value-format="yyyy-MM-dd"
              :picker-options="pickerOptions"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="订/退单号：">
            <el-input style="width: 203px" v-model="searchList.name" clearable @input="change($event)" placeholder="姓名/手机号"></el-input>
          </el-form-item>
          <el-form-item label="单据类型：">
            <el-select v-model="searchList.companyType" clearable>
              <el-option label="全部" value="0"></el-option>
              <el-option label="订单入账" value="1"></el-option>
              <el-option label="退款" value="2"></el-option>
              <el-option label="交易手续费" value="3"></el-option>
              <el-option label="交易手续费退回" value="4"></el-option>
              <el-option label="提现" value="5"></el-option>
              <el-option label="提现手续费" value="6"></el-option>
              <el-option label="充值" value="7"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="支付流水号：">
            <el-input style="width: 203px" v-model="searchList.wxNumber" clearable @input="change($event)" placeholder="姓名/手机号"></el-input>
          </el-form-item>
          <el-form-item label="账户：">
            <el-select v-model="searchList.companyType" clearable>
              <el-option label="店铺余额" value="1"></el-option>
              <el-option label="储值资金" value="2"></el-option>
              <el-option label="标记资金" value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container mt10" shadow="never">
      <div class="flex-row-start" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>账户流水</span>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card style="margin-top:20px;" class="filter-container" shadow="never">
        <el-table :data="list" tooltip-effect="dark" style="width: 100%">
          <el-table-column label="业务单据号" width="240" prop="businessNumber"></el-table-column>
          <el-table-column label="支付流水号" width="300" prop="wxNumber"></el-table-column>
          <el-table-column label="单据类型/交易类型" width="200" prop="updateStatusName"></el-table-column>
          <el-table-column label="名称" width="400" prop="description"></el-table-column>
          <el-table-column label="账单金额" width="200" prop="amount"></el-table-column>
          <!-- <el-table-column label="账户余额" width="200">
            <template slot-scope="scope">
              {{scope.row.minOriginPrice ? scope.row.minOriginPrice : '-'}}
            </template>
          </el-table-column> -->
          <el-table-column label="收支类型" width="200">
            <template slot-scope="scope">
              {{scope.row.minOriginPrice ? scope.row.minOriginPrice : '-'}}
            </template>
          </el-table-column>
          <el-table-column label="支付方式" width="200">
            <template slot-scope="scope">
              {{scope.row.minOriginPrice ? scope.row.minOriginPrice : '-'}}
            </template>
          </el-table-column>
          <el-table-column label="创建时间" width="200">
            <template slot-scope="scope">
              {{scope.row.minOriginPrice ? scope.row.minOriginPrice : '-'}}
            </template>
          </el-table-column>
          <el-table-column label="入账时间" width="200">
            <template slot-scope="scope">
              {{scope.row.minOriginPrice ? scope.row.minOriginPrice : '-'}}
            </template>
          </el-table-column>
          <el-table-column label="账户" width="200">
            <template slot-scope="scope">
              {{scope.row.minOriginPrice ? scope.row.minOriginPrice : '-'}}
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="$router.push({path:'/commodity/commodityList/commodityInfo',query:{productId:scope.row.id}})">详情</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="flex-row-end" style="padding-top:20px;">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :page-sizes="[5,10,15,20]"
            :page-size="searchList.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="listTotal">
          </el-pagination>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopTradBill } from '@/api/assetsManage/capitalManage'
export default {
  data(){
    return{
      searchList:{
        pageSize:5,
        pageNum:1
      },
      pickerOptions: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime()- 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
      list:[],
      listTotal:0
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData()
  },
  methods:{
    getData(){
      getShopTradBill(this.searchList).then(res=>{
        console.log('交易账单列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    reset(){},
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .wordInfo{
    margin-left: 80px;
    .word{
      margin-right: 40px;
      font-size: 16px;
      font-weight: bold;
    }
  }
</style>